import React from 'react'

class SubmitBtn extends React.Component {
    
    render() {
        return (
            <div className="submitBtn">
                <button
                    className='btn'
                    disabled={this.props.disabled}
                    onClick={ () => this.props.onClick() }
                    >
                        { this.props.text }
                    </button>
            </div>
        )
    }
    
}

export default SubmitBtn
